@media screen and (min-width: 500px) {
  .container {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .Rank {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    margin-top: 80px;
    overflow:visible;
    width: 850px;
  }

  .filters {
    grid-column: span 3;
    display: flex;
    width: 100%;
    justify-content: flex-start;
    gap: 16px;
  }

  .MuiIconButton-root {
    display: none !important;
  }

}

@media screen and (max-width: 500px) {
  
  .Rank {
    display: grid;
    height: 100%;
    overflow: hidden;
    grid-template-rows: 24px 1fr;
    grid-template-columns: 400px;
    row-gap: 16px;
    justify-content: center;
    align-items: center;
    justify-items: center;
    margin-top: 16px;
  }

  .filters {
    grid-column: span 3;
    display: flex;
    width: 100%;
    justify-content: flex-start;
    gap: 16px;
  }

  #artist-filter {
    display: none;
  }

  #year-filter {
    display: none;
  }

  #ranking-filter {
    display: none;
  }

  .MuiIconButton-root {
    justify-self: end !important;
  }

  .container {
    height: calc(100vh - 71px);
    overflow: hidden;
  }

}
